import { Component, OnInit, Input } from '@angular/core';
import { TextHelperService } from '../../shared/text-helper.service';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';

@Component({
  selector: 'isma-case-study',
  templateUrl: './case-study.component.html',
  styleUrls: ['./case-study.component.styl']
})
export class CaseStudyComponent implements OnInit {
  @Input() sceneID: string = '';
  private content:any;

  private subTitles: string[] = [];
  private titleText: string[] = [];

  private subTitleRegex = /^\$\$sCaseStudySubTitle\d+$/;
  private titleRegex = /^\$\$sCaseStudyText\d+$/;

  constructor(private textHelper: TextHelperService, private calcService: CalcService, private textEngineService: TextService) { }

  ngOnInit() {
    this.content = this.textEngineService.getScene(this.sceneID);
    let table = this.content.table;

    this.subTitles = []; 
    this.titleText = [];
    for (let key in table) {
      if (this.subTitleRegex.test(key))
          this.subTitles.push(this.content.getValue(key.replace('$$s','')));
      if (this.titleRegex.test(key))
          this.titleText.push(this.content.getValue(key.replace('$$s','')));
    }
  }

}



