import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { IsomerCoreModule } from '@btsdigital/ngx-isomer-core';
import { CarouselModule } from 'ngx-bootstrap/carousel';
import { SortablejsModule } from 'angular-sortablejs';

import { FormsModule } from '@angular/forms';
import { LeaderboardComponent } from './leaderboard/leaderboard.component';
import { SubmetrixballComponent } from './submetrixball/submetrixball.component';
import { HeaderComponent } from './header/header.component';
import { SharedModule } from '../shared/shared.module';
import { TimerComponent } from './timer/timer.component';
import { RadioSelectionComponent } from '../components/radio-selection/radio-selection.component';
import { CalcdropdownComponent } from './calcdropdown/calcdropdown.component';
import { CalcStepperComponent } from './calc-stepper/calcstepper.component';
import { HotspotComponent } from '../components/hotspot/hotspot.component';
import { BranchingComponent } from '../components/branching/branching.component';
import { SingleSelectComponent } from './single-select/single-select.component';
import { MultiSelectComponent } from './multi-select/multi-select.component';
import { DraglistComponent } from './draglist/draglist.component';
import { QuizComponent } from './quiz/quiz.component';
import { ToggleSliderComponent } from './toggle-slider/toggle-slider.component';
import { TrueFalseSelectComponent } from './true-false/truefalse-select.component';
import { FreeTextComponent } from './freetext/freetext.component';
import { FreeTextCheckboxComponent } from './freetext-checkbox/freetext-checkbox.component';
import { CalcTextareaComponent } from './calc-textarea/calc-textarea.component';
import { InfoComponent } from './info/info.component';
import { IncomeStatementComponent } from './income-statement/income-statement.component';
import { RoicComponent } from './roic/roic.component';
import { ChartsModule } from '../views/charts/charts.module';
import { CaseStudyComponent } from './case-study/case-study.component';
import { ScrollToDirective } from './scrollTo.directive';

@NgModule({
   imports: [
      CommonModule,
      RouterModule,
      SharedModule,
      CarouselModule.forRoot(),
      IsomerCoreModule,
      FormsModule,
      SortablejsModule,
      ChartsModule
   ],
   declarations: [
      SingleSelectComponent,
      LeaderboardComponent,
      SubmetrixballComponent,
      HeaderComponent,
      TimerComponent,
      RadioSelectionComponent,
      CalcdropdownComponent,
      CalcStepperComponent,
      HotspotComponent,
      BranchingComponent,
      DraglistComponent,
      MultiSelectComponent,
      QuizComponent,
      ToggleSliderComponent,
      TrueFalseSelectComponent,
      FreeTextComponent,
      CalcTextareaComponent,
      FreeTextCheckboxComponent,
      InfoComponent,
      IncomeStatementComponent,
      RoicComponent,
      CaseStudyComponent,
      ScrollToDirective
   ],
   exports: [
      SingleSelectComponent,
      LeaderboardComponent,
      SubmetrixballComponent,
      HeaderComponent,
      CalcdropdownComponent,
      CalcStepperComponent,
      RadioSelectionComponent,
      HotspotComponent,
      BranchingComponent,
      DraglistComponent,
      MultiSelectComponent,
      QuizComponent,
      ToggleSliderComponent,
      TrueFalseSelectComponent,
      FreeTextComponent,
      CalcTextareaComponent,
      FreeTextCheckboxComponent,
      InfoComponent,
      IncomeStatementComponent,
      RoicComponent,
      CaseStudyComponent
   ],
   entryComponents: [
      SingleSelectComponent,
      RadioSelectionComponent,
      HotspotComponent,
      DraglistComponent,
      MultiSelectComponent
   ]
})
export class ComponentsModule { }



