import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { SplashComponent } from './splash/splash.component';
import { LogoutComponent } from './logout/logout.component';
import { ModelLoaderService } from '../model/model-loader.service';
import { BootstrapService } from './bootstrap.service';
import { IsomerCoreModule, JsCalcConnectorService } from '@btsdigital/ngx-isomer-core';
import { BsDropdownModule, AlertModule, ModalModule,
  AccordionModule, ButtonsModule, CarouselModule,
  TabsModule, TooltipModule, PopoverModule } from 'ngx-bootstrap';
import { LoggerService } from '@btsdigital/pulseutilities';
import { TextHelperService } from './text-helper.service';
import { CalcOutputComponent } from './calc-output/calc-output.component';
import { EpsChartComponent } from './eps-chart/eps-chart.component';
import { ExternalModelComponent } from './external-model/external-model.component';
import { TooltipComponent } from './tooltip/tooltip.component';
import { SyncComponentComponent } from './sync-component/sync-component.component';
import { ContentHostDirective } from './content-host.directive';
import { FileSaver } from './utils/filesaver';
import { CsvFileReaderService } from './utils/csvfilereader';
import { ImportFileComponent } from './importfile/importfile.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RouterModule,
    IsomerCoreModule,
    BsDropdownModule.forRoot(),
    AlertModule.forRoot(),
    ModalModule.forRoot(),
    AccordionModule.forRoot(),
    ButtonsModule.forRoot(),
    CarouselModule.forRoot(),
    TabsModule.forRoot(),
    TooltipModule.forRoot(),
    PopoverModule.forRoot()
  ],
  declarations: [SplashComponent, LogoutComponent, CalcOutputComponent, EpsChartComponent,
    ExternalModelComponent, TooltipComponent, SyncComponentComponent, ContentHostDirective, ImportFileComponent],
  providers: [ ModelLoaderService, BootstrapService, JsCalcConnectorService, LoggerService,
    TextHelperService, FileSaver, CsvFileReaderService],
  exports: [
    SplashComponent, LogoutComponent, IsomerCoreModule, ExternalModelComponent,
    AlertModule, BsDropdownModule, ModalModule, AccordionModule,
    ButtonsModule, CarouselModule, TabsModule, TooltipModule,
    PopoverModule, FormsModule, RouterModule, CommonModule, CalcOutputComponent,
    EpsChartComponent, TooltipComponent, SyncComponentComponent, ContentHostDirective, ImportFileComponent
  ]
})
export class SharedModule { }



