import { Directive, AfterViewInit, ElementRef } from '@angular/core';

@Directive({
  selector: '[ismaScrollTo]'
})
export class ScrollToDirective implements AfterViewInit {

  constructor(private elRef:ElementRef) {}

  ngAfterViewInit(){
    try{
      this.elRef.nativeElement.scrollIntoView({ behavior: 'smooth' });
    }catch(e){
      //try different method of scrolling. as some browsers do not support the scroll into view options
      try{
        this.elRef.nativeElement.scrollIntoView();
      }catch(e){
        console.log("Tried Scrolling to element but failed");
      }
    }
  }

}



