import { Component, OnInit } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';

@Component({
  selector: 'isma-income-statement',
  templateUrl: './income-statement.component.html',
  styleUrls: ['./income-statement.component.styl']
})
export class IncomeStatementComponent implements OnInit {
	private calculatorInputRefs = ['tInput_Rev_A', 'tlInput_COGS_A', 'tlInput_OE_A', 'tlInput_Shares_A']
	private calculatorInputValues = [1000, 480, 300, 4.415]

  constructor(private calcService: CalcService) { }

  ngOnInit() {
  	this.resetValues();
  }

  resetValues(){
  	for(let i=0; i< this.calculatorInputRefs.length; i++){
  		this.calcService.setValue(this.calculatorInputRefs[i], this.calculatorInputValues[i]);
  	}
  }

}



