import { Component, Input, OnInit, Output } from '@angular/core';
import * as _ from 'lodash';
import { Subject } from 'rxjs/Subject';

@Component({
  selector: 'isma-hotspot',
  templateUrl: './hotspot.component.html',
  styleUrls: ['./hotspot.component.styl']
})
export class HotspotComponent implements OnInit {

  @Input() data: any;
  @Output() optionSelected: Subject<any> = new Subject<any>();

  private baseImageUrl: string = 'assets/images/';
  public mapImageUrl: string = this.baseImageUrl + 'map-hotspot.png';
  public checkedImageUrl: string = this.baseImageUrl + 'marker_checked.svg';
  public uncheckedImageUrl: string = this.baseImageUrl + 'marker_unchecked.svg';
  public markers: Array<any> = [];

  public rangeRef: Array<any>;
  public xAxis: Object;
  public yAxis: Object;
  public series: Object;
  public chartOptions: Object;
  public selectedMarker: Object;

  constructor() { }

  ngOnInit() {
    this.initChartData();

    if (this.data && this.data.mapData) {
      this.data.mapData = this.data.mapData.split('&quot;').join('"');
      this.data.mapData = JSON.parse(this.data.mapData);
      this.mapImageUrl = this.baseImageUrl + this.data.mapData.image;
      this.checkedImageUrl = this.baseImageUrl + this.data.mapData.checkedIcon;
      this.uncheckedImageUrl = this.baseImageUrl + this.data.mapData.uncheckedIcon;
    }

    if (this.data) {
      let count = 1;
      _.forEach(this.data, (value, key: string) => {
        if (key.indexOf('alt' + count) > -1) {
          const obj: Object = {};
          obj['name'] = this.data[key];
          obj['top'] = this.data[key + 'Top'];
          obj['left'] = this.data[key + 'Left'];
          obj['markerImage'] = this.uncheckedImageUrl;
          this.markers.push(obj);
          count++;
        }
      });
    }
  }

  checkMarker(index: number) {
    _.each(this.markers, (n, i) => {
      this.markers[i].markerImage = this.uncheckedImageUrl;
      if (i === index) {
        this.markers[i].markerImage = this.checkedImageUrl;
      }
    });
    this.optionSelected.next((index + 1));
    this.selectedMarker = this.markers[index];
    this.selectedMarker['index'] = index + 1;
  }

  initChartData() {
    this.rangeRef = [
      [
        'tlOutputOfficeLocationLevels1_1',
        'tlOutputOfficeLocationLevels2_1',
        'tlOutputOfficeLocationLevels3_1',
        'tlOutputOfficeLocationLevels4_1',
        'tlOutputOfficeLocationLevels5_1'
      ],
      [
        'tlOutputOfficeLocationLevels1_2',
        'tlOutputOfficeLocationLevels2_2',
        'tlOutputOfficeLocationLevels3_2',
        'tlOutputOfficeLocationLevels4_2',
        'tlOutputOfficeLocationLevels5_2'
      ],
      [
        'tlOutputOfficeLocationLevels1_3',
        'tlOutputOfficeLocationLevels2_3',
        'tlOutputOfficeLocationLevels3_3',
        'tlOutputOfficeLocationLevels4_3',
        'tlOutputOfficeLocationLevels5_3'
      ]
    ];

    this.xAxis = {
      categories: ['Employee Exp', 'Cost', 'Location', 'Connectivity', 'Facilities']
    };

    this.yAxis = {
      gridLineInterpolation: 'polygon'
    };

    this.series = [{
      name: '1. Goregaon'
    }, {
      name: '2. Parel'
    }, {
      name: '3. Navi Mumbai'
    }];

    this.chartOptions = {
      chart: {
        polar: true,
        type: 'line',
        width: 450,
        className: 'hotspot'
      },
      title: {
        text: 'Comparison'
      },
      pane: {
        size: '65%'
      },
      tooltip: {
        valuePrefix: ''
      },
      legend: {
        verticalAlign: 'top',
        y: 30
      }, plotOptions: {
        line: {
          marker: {
            enabled: false
          }
        }
      }
    };
  }
}



