import { Component, OnInit, Input } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import * as _ from 'lodash';


@Component({
  // tslint:disable-next-line:component-selector
  selector: 'isma-submetrixball',
  templateUrl: './submetrixball.component.html',
  styleUrls: ['./submetrixball.component.styl']
})
export class SubmetrixballComponent implements OnInit {
  @Input() submetrixballData: any;
  @Input() classCondition: number;
  constructor(private calcService: CalcService) { }

  ngOnInit() { }

  getMetricClass(rangeRef: string) {
    let val = this.calcService.getValueForYear(rangeRef) as any;
    if (val) {
      val = parseFloat(val);
      if (val < 4) {
        return 'low';
      } else if (val < 7 && val >= 4) {
        return 'medium';
      } else if (val >= 7) {
        return 'high';
      }
    }
  }

}



