import { Injectable } from '@angular/core';
import { CanDeactivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { EndOfRoundFeedbackComponent } from './end-of-round-feedback.component';
import { environment } from '../../../environments/environment';

@Injectable()
export class CanDeactivateEndOfRoundFeedbackGuard implements CanDeactivate<EndOfRoundFeedbackComponent> {
  canDeactivate(
    component: EndOfRoundFeedbackComponent,
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot) {
      if (environment.production === false) {
        return true;
      }
      return component.canDeactivate();
  }
}



