import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subject } from 'rxjs/Rx';
import { TextHelperService } from '../../shared/text-helper.service';
import * as _ from 'lodash';

@Component({
  selector: 'isma-single-select',
  templateUrl: './single-select.component.html',
  styleUrls: ['./single-select.component.styl']
})
export class SingleSelectComponent implements OnInit {

  @Input() public data: any;
  @Input() public hideLabel: boolean = false;

  choices: any[] = [];
  answer = 0;
  correctAnswer = 0;
  id: string;
  scene: any;
  isAnswered: boolean = false;
  public readyToSubmit = false;
  allCorrect = false;

  @Output() continue: EventEmitter<any> = new EventEmitter<any>();

  @Output() selectedAnswer: Subject<any> = new Subject<any>();

  constructor(private calcService: CalcService, private textHelper: TextHelperService) { }

  ngOnInit() {
    if (this.data) {
      this.id = this.data.ID;
      this.scene = this.data.scene;
      this.allCorrect = (this.data.AllCorrect) ? this.data.AllCorrect : false


      const curVal = this.calcService.getValue('tlInput' + this.id);

      if (curVal != "0") {
        this.answer = curVal.charCodeAt(0) - 64;
      }

      this.choices = this.textHelper.getSingleSelectChoices(this.id);

      this.correctAnswer = 1 + _.findIndex(this.choices, (obj) => { return obj['feedback'] == 'Correct' })

      let readValue = this.calcService.getValue('tlInput_READ_' + this.id);
      this.isAnswered = readValue == "0" ? false : true;

      if(this.isAnswered){
        this.onContinue(true);
      }
    }
  }

  onSelect($event) {
    this.readyToSubmit = true;
    this.selectedAnswer.next(this.answer);
  }

  onSubmit(){
    this.isAnswered = true;
    this.calcService.setValue('tlInput' + this.id, String.fromCharCode(64 + (+this.answer)));
    this.calcService.setValue('tlInput_READ_' + this.id, 1);
  }

  onContinue(alreadySubmitted: boolean){
    this.continue.emit(alreadySubmitted)
  }
}



