import { Injectable } from '@angular/core';
import { TextService } from '@btsdigital/ngx-isomer-core';
import * as Collections from 'typescript-collections';
import { AppConstants } from './app-constants';
import { PageType } from './page-type.enum';
import * as _ from 'lodash';

@Injectable()
export class TextHelperService {
  private textContent: Collections.Dictionary<string, any>;
  private processedScenes: any[];
  private investments: any[] = [];
  private gtmInvestments: any[] = [];
  private rawData: any;
  private teamData = [];
  private clients = [];
  private clientsPerRound = {};

  constructor(private textService: TextService) {
    this.textContent = this.textService.content;
  }

  getInitiatives(): Collections.Dictionary<string, any> {
    const initiatives = new Collections.Dictionary<string, any>();
    this.textContent.keys()
      .forEach((key) => {
        if (this.textContent.getValue(key).getValue('PageType') === 'Initiative') {
          initiatives.setValue(key, this.textContent.getValue(key));
        }
      });
    return initiatives;
  }

  getFeedback(): Collections.Dictionary<string, any> {
    const feedbacks = new Collections.Dictionary<string, any>();
    this.textContent.keys()
      .forEach((key) => {
        if (this.textContent.getValue(key).getValue('PageType') === 'Feedback') {
          feedbacks.setValue(key, this.textContent.getValue(key));
        }
      });
    return feedbacks;
  }


  getSingleSelectChoices(id: string): Array<any> {
    const localChoices = [],
      localObj = this.textService.getScene(id),
      keyArr: any[] = localObj.keys();
    let i = 1,
      moreChoices = false;

    keyArr.forEach((key: any) => {
      if (key.indexOf('alt' + i) !== -1) {
        moreChoices = true;
      }
    });

    while (moreChoices) {
      let localLabel = '', localFeedback = '',
        localDesc = '';
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i) !== -1) && (key.length < 6)) {
          localLabel = localObj.getValue(key);
        }
      });
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i) !== -1) && (key.indexOf('_narrative') !== -1)) {
          localDesc = localObj.getValue(key);
        }
      });

      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i) !== -1) && (key.indexOf('_feedback') !== -1)) {
          localFeedback = localObj.getValue(key);
        }
      });
      localChoices.push({
        'value': String.fromCharCode(64 + i),
        'label': localLabel,
        'desc': localDesc,
        'feedback': localFeedback
      });
      i++;
      moreChoices = false;
      keyArr.forEach((key: any) => {
        if (key.indexOf('alt' + i) !== -1) {
          moreChoices = true;
        }
      });
    }
    return localChoices;
  }

  getScenes(): Collections.Dictionary<string, any> {
    return this.textContent;
  }

  getRawScenes(): any {
    if (this.rawData !== void 0) {
      return this.rawData;
    } else {
      return this.rawData = this.processSceneData();
    }
  }

  processSceneData() {
    this.processedScenes = [];
    this.getScenes().forEach((key, sceneData: Collections.Dictionary<string, any>) => {
      const scene: any = {},
        ID = key,
        decisionType = sceneData.getValue('DecisionType'),
        regex = /(?:L|A)(\d)(?:E|O|M|S|A|ReadUpdate)(\d+)_R(\d+)/,
        matches = regex.exec(ID);

      let pageType = sceneData.getValue('PageType');

      if (typeof pageType === 'string') {
        pageType = pageType.trim();
      }
      // match expression | RoundIndex | Event/Opp/ReadUpdate Index

      sceneData.forEach((_key, value) => {
        scene[_key] = value;
      });
      scene['pinViewed'] = 0;
      scene['pinVisible'] = false;

      scene['DecisionType'] = (decisionType === void 0) ? 'singleselect' : decisionType;

      let pinType = 0;
      switch (pageType) {
        case 'Opportunity':
          pinType = 1;
          if (matches !== null) {
            scene['pinTrigger'] = AppConstants.PREFIX.OPPORTUNITY_TRIGGER + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];
            scene['pinDecisionVariable'] = AppConstants.PREFIX.OPPORTUNITY + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];
            scene['pinViewedVariable'] = AppConstants.PREFIX.OPPORTUNITY_READ + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];
          }
          break;
        case 'Event':
          pinType = 2;
          if (matches !== null) {
            scene['pinTrigger'] = AppConstants.PREFIX.EVENT_TRIGGER + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];
            scene['pinDecisionVariable'] = AppConstants.PREFIX.EVENT + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];
            scene['pinViewedVariable'] = AppConstants.PREFIX.EVENT_READ + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];

            if(scene['DecisionType'] == "DragDrop"){
              const dragdropData = this.processDragDropData(sceneData);
              scene['dragdropData'] = dragdropData;
            }
          }
          break;
        case 'Matrix':
          pinType = 12;
          if (matches !== null) {
            const matrixData = this.processMatixData(sceneData);
            scene['pinTrigger'] = AppConstants.PREFIX.MATRIX_TRIGGER + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];
            const pinDecisionVariables = [];
            for (let i = 1; i < matrixData.length; i++) {
              pinDecisionVariables.push(AppConstants.PREFIX.MATRIX + '1' + String.fromCharCode(64 + i) + AppConstants.SUFFIX.ROUND + matches[1]);
            }
            scene['pinDecisionVariable'] = pinDecisionVariables;
            scene['pinViewedVariable'] = AppConstants.PREFIX.MATRIX_READ + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];
            scene['matrixData'] = matrixData;
          }
          break;
        case 'Allocation':
          pinType = 13;
          if (matches !== null) {
            scene['pinTrigger'] = AppConstants.PREFIX.ALLOCATION_TRIGGER + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];
            scene['pinViewedVariable'] = AppConstants.PREFIX.ALLOCATION_READ + matches[2] + AppConstants.SUFFIX.ROUND + matches[1];
          }
          break;
        case 'Read Update':
          pinType = 3;
          if (matches !== null) {
            const splitted = ID.split('R' + matches[1])[1];
            scene['pinTrigger'] = AppConstants.PREFIX.INFO_UPDATE_TRIGGER + splitted + AppConstants.SUFFIX.ROUND + matches[1];
            scene['pinDecisionVariable'] = AppConstants.PREFIX.INFO_UPDATE + splitted + AppConstants.SUFFIX.ROUND + matches[1];
            scene['pinViewedVariable'] = AppConstants.PREFIX.INFO_UPDATE_READ + splitted + AppConstants.SUFFIX.ROUND + matches[1];
          }
          break;
        case 'GME': // Initiatives
          pinType = 11;
          scene.isSelected = 0;
          scene.isChecked = 0;
          scene.isEnabled = 1;
          scene.round = ID.slice(1, 2);
          scene.pinID = ID.slice(2);
          const investmentLetter = ID[2];
          this.investments.push(scene);
          const actionIndex = ID.split(/(P)/)[1];
          const roundIndex = ID.split('X')[0].split('R')[1];
          scene['pinTrigger'] = AppConstants.PREFIX.LEADER_ACTIONS + actionIndex + AppConstants.SUFFIX.ROUND + roundIndex;
          scene['pinDecisionVariable'] = AppConstants.PREFIX.LEADER_ACTIONS + actionIndex + AppConstants.SUFFIX.ROUND + roundIndex;
          break;
        case 'VAS': // leader Actions
          pinType = 10;
          scene.isSelected = 0;
          scene.isChecked = 0;
          scene.isEnabled = 1;
          scene.round = ID.slice(1, 2);
          scene.pinID = ID.slice(2);
          this.gtmInvestments.push(scene);
          const initiativeIndex = ID.split(/(X|Y)/)[1];
          const roundIndex1 = ID.split('X')[0].split('R')[1];
          scene['pinTrigger'] = AppConstants.PREFIX.LEADER_ACTIONS + actionIndex + AppConstants.SUFFIX.ROUND + roundIndex1;
          scene['pinDecisionVariable'] = AppConstants.PREFIX.LEADER_ACTIONS + initiativeIndex + AppConstants.SUFFIX.ROUND + roundIndex1;
          break;
        case 'Quiz':
          scene['pinTrigger'] = AppConstants.PREFIX.INFO_UPDATE_TRIGGER + sceneData.getValue('ID');
          scene['pinDecisionVariable'] = AppConstants.PREFIX.INFO_UPDATE + sceneData.getValue('ID');
          scene['pinViewedVariable'] = AppConstants.PREFIX.INFO_UPDATE_READ + sceneData.getValue('ID');
          break;
      }
      scene.pinType = pinType;
      this.processedScenes.push(scene);

      const client = sceneData.getValue('Client') ? parseInt(sceneData.getValue('Client')) : false;

      if (client > 0 && matches && matches.length > 0) {
        // console.log('Client', client, matches);
        if (this.clientsPerRound['R' + matches[3]]) {
          if (this.clientsPerRound['R' + matches[3]].indexOf(client) === -1) {
            this.clientsPerRound['R' + matches[3]].push(client);
          }
        } else {
          this.clientsPerRound['R' + matches[3]] = [client];
        }
      }

    });
    // this.initClientsPerRound(clientsPerRound);
    return {
      content: this.processedScenes,
      investments: this.investments,
      gtmInvestments: this.gtmInvestments
    };
  }

  processMatixData(sceneData: Collections.Dictionary<string, any>): any {
    const matrixData = [];
    if (sceneData && sceneData.keys() && sceneData.keys().length > 0) {
      matrixData.push({ isHeader: true, optionKey: sceneData.getValue('name') });
      const cols = [];
      _.each(sceneData.keys(), (key: string) => {
        if (key.indexOf('alt') > -1 && key.indexOf('_feedback') == -1) {
          matrixData.push({ optionKey: sceneData.getValue(key), correctKey: sceneData.getValue(key + '_feedback') });
        } else if (key.indexOf('col') > -1) {
          cols.push(sceneData.getValue(key));
        }
      });
      matrixData[0]['optionsRef'] = cols;
    }
    return matrixData;
  }

  processDragDropData(sceneData: Collections.Dictionary<string, any>): any {
    const dragdropData = [];
    const eventID = sceneData.getValue('ID')
    const eventIndex = eventID[eventID.length - 1];
    const round = eventID[1];
    
    if (sceneData && sceneData.keys() && sceneData.keys().length > 0) {
      const cols = [];
      let i = 1;
      _.each(sceneData.keys(), (key: string) => {
        if (key.indexOf('alt') > -1) {
          dragdropData.push({ 
            label: sceneData.getValue(key), 
            range: AppConstants.PREFIX.EVENT + eventIndex + '_' + i + AppConstants.SUFFIX.ROUND + round
          });
          i++;
        }
      });
    }
    return dragdropData;
  }

  processTeamData(): any {
    if (this.teamData.length === 0) {
      const data = this.getRawScenes().content;
      let onlyTeams = {};
      for (let i = 0; i < data.length; i++) {
        onlyTeams = Object.keys(data[i]).filter((k) => {
          return k.startsWith('Team');
        }).reduce((newData, k) => {
          newData[k] = data[i][k];
          return newData;
        }, {});

        if (Object.keys(onlyTeams).length > 0) {
          break;
        }
      }
      this.processTeams(onlyTeams, 1);
    }
    return this.teamData;
  }

  private processTeams(teamData: any, index: number) {
    const team = Object.keys(teamData).filter((k) => {
      return k.startsWith('Team' + index);
    }).reduce((newData, k) => {
      let key = k;
      key = key.split('Team' + index + '_').join('').toLowerCase();

      newData[key] = teamData[k];

      if (teamData[k].indexOf('{') > -1 && teamData[k].indexOf('}') > -1) {
        teamData[k] = teamData[k].split(`'`).join('');
        teamData[k] = teamData[k].split('}').join('');
        teamData[k] = teamData[k].split('{').join('');
        teamData[k] = teamData[k].split(',');

        for (let j = 0; j < teamData[k].length; j++) {
          teamData[k][j] = teamData[k][j].trim();
        }

        newData[key] = teamData[k];
      }
      return newData;
    }, {});

    if (Object.keys(team).length > 0) {
      this.teamData.push(team);
      this.processTeams(teamData, ++index);
    }
  }

  getAllClients() {
    if (this.clients.length === 0) {
      const data = _.filter(this.getRawScenes().content, { ID: 'GEN' })[0];
      const onlyClients = Object.keys(data).filter((k) => {
        return k.startsWith('Client');
      }).reduce((newData, k) => {
        newData[k] = data[k];
        return newData;
      }, {});
      this.processClients(onlyClients, 1);
    }
    return this.clients;
  }

  private processClients(clientData: any, index: number) {
    const client = Object.keys(clientData).filter((k) => {
      return k.startsWith('Client' + index);
    }).reduce((newData, k) => {
      newData[k] = clientData[k];
      return newData;
    }, {});

    if (Object.keys(client).length > 0) {
      this.clients.push(client);
      this.processClients(clientData, ++index);
    }
  }

  getActiveClientsForRound(round: string) {
    if (this.clientsPerRound['R' + round]) {
      const activeClients = new Array(this.clients.length).fill(0);
      _.each(this.clientsPerRound['R' + round], (n) => {
        activeClients[n - 1] = 1;
      });
      return activeClients;
    } else {
      throw new Error('No Clients for round ' + round);
    }
  }

  getMultiSelectChoices(id: string): Array<any> {
    const localChoices = [],
      localObj = this.textService.getScene(id),
      keyArr: any[] = localObj.keys();
    let i = 1,
      moreChoices = false;

    keyArr.forEach((key: any) => {
      if (key.indexOf('alt' + i) !== -1) {
        moreChoices = true;
      }
    });

    while (moreChoices) {
      let localLabel = '',
        localDesc = '',
        localRange = '',
        localFeedback = '';
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i) !== -1) && (key.length < 4 + i.toString().length)) {
          localLabel = localObj.getValue(key);
        }
      });
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('narrative') !== -1)) {
          localDesc = localObj.getValue(key);
        }
      });
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('range') !== -1)) {
          localRange = localObj.getValue(key);
        }
      });
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('feedback') !== -1)) {
          localFeedback = localObj.getValue(key);
        }
      });
      localChoices.push({
        'label': localLabel,
        'desc': localDesc,
        'range': localRange,
        'feedback': localFeedback
      });
      i++;
      moreChoices = false;
      keyArr.forEach((key: any) => {
        if (key.indexOf('alt' + i) !== -1) {
          moreChoices = true;
        }
      });
    }
    return localChoices;
  }

  getPrioritizationChoices(id: string): Array<any> {
    const localChoices = [],
      localObj = this.textService.getScene(id),
      keyArr: any[] = localObj.keys();
    let i = 1,
      moreChoices = false;

    keyArr.forEach((key: any) => {
      if (key.indexOf('alt' + i) !== -1) {
        moreChoices = true;
      }
    });

    while (moreChoices) {
      let localLabel = '',
        localDesc = '',
      localFeedback = '',
      localFeedbackText = '';
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i) !== -1) && (key.length < 4 + i.toString().length)) {
          localLabel = localObj.getValue(key);
        }
      });
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('narrative') !== -1)) {
          localDesc = localObj.getValue(key);
        }
      });
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('feedback') !== -1) && (key.indexOf('_') == key.lastIndexOf('_'))) {
          localFeedback = localObj.getValue(key);
        }
      });
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + i + '_') !== -1) && (key.indexOf('feedback_text') !== -1)) {
          localFeedbackText = localObj.getValue(key);
        }
      });
      localChoices.push({
        'label': localLabel,
        'desc': localDesc,
        'feedback': localFeedback,
        'feedbackText': localFeedbackText
      });
      i++;
      moreChoices = false;
      keyArr.forEach((key: any) => {
        if (key.indexOf('alt' + i) !== -1) {
          moreChoices = true;
        }
      });
    }

    const localMatches = []
    let j = 1;
    let char = String.fromCharCode(64 + j);
      moreChoices = false;

    keyArr.forEach((key: any) => {
      if (key.indexOf('alt' + char) !== -1) {
        moreChoices = true;
      }
    });

    while (moreChoices) {
      let localLabel = '',
        localDesc = '',
        localFeedback = '';
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + char) !== -1) && (key.length < 4 + char.toString().length)) {
          localLabel = localObj.getValue(key);
        }
      });
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + char + '_') !== -1) && (key.indexOf('narrative') !== -1)) {
          localDesc = localObj.getValue(key);
        }
      });
      keyArr.forEach((key: any) => {
        if ((key.indexOf('alt' + char + '_') !== -1) && (key.indexOf('feedback') !== -1)) {
          localFeedback = localObj.getValue(key);
        }
      });
      localMatches.push({
        'label': localLabel,
        'desc': localDesc,
        'feedback': localFeedback
      });
      
      j++;
      char = String.fromCharCode(64 + j)
      moreChoices = false;
      keyArr.forEach((key: any) => {
        if (key.indexOf('alt' + char) !== -1) {
          moreChoices = true;
        }
      });
    }

    let finalChoices = localMatches.length > 0 ? [localMatches,localChoices] : [localChoices]
    return finalChoices;
  }


  getProcessedScenes(){
    return this.processedScenes;
  }

}



