import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { TextService, CalcService } from '@btsdigital/ngx-isomer-core';
import { SortablejsModule, SortablejsOptions } from 'angular-sortablejs';
import { Subject } from 'rxjs/Rx';
import { TextHelperService } from '../../shared/text-helper.service';

@Component({
    selector: 'isma-draglist',
    templateUrl: './draglist.component.html',
    styleUrls: ['./draglist.component.styl']
})
export class DraglistComponent implements OnInit {
    @Input() data: any;
    @Output() continue: EventEmitter<any> = new EventEmitter<any>();

    id: string;
    scene: any;
    isAnswered: boolean = false;
    choices: any[] = [];

    fixedList = [];
    feedbackList = [];

    @Input() yearRef: string;
    private _items = [];
    private options;

    @Output() valuesUpdated: Subject<any> = new Subject<any>();

    constructor(private calcService: CalcService, private textHelper: TextHelperService) {
        this.options = {
            onUpdate: (event: any) => {
                // this.updateValuesInModel();
            }
        };
    }

    ngOnInit() {
        if(this.data){
            this.id = this.data.ID;
            this.scene = this.data.scene;

            this.processData();
        }
    }

    processData(){
        this.choices = this.textHelper.getPrioritizationChoices(this.id);

        let readValue = this.calcService.getValue('tlInput_READ_' + this.id);
        this.isAnswered = readValue == "0" ? false : true;

        if (this.isAnswered) {
            this.onContinue(true);
        }

        this.processItems();
    }

    processItems() {
        this._items = [];
        for (let j = 0; j < this.choices.length;j++){
            const localItems = [];
            let localArray = this.choices[j];
            for (let i = 0; i < localArray.length; i++) {
                let obj = {
                    label: localArray[i]['desc'],
                    order: localArray[i]['label'],
                    feedback: localArray[i]['feedback'],
                    feedbackText: localArray[i]['feedbackText']
                }
                localItems.push(obj);
            }
            this.sortByKey(localItems, "order");
            this._items.push(localItems);
        }

        //Use .slice method to create copy of array then copy it to avoid Call By Reference
        if(this._items.length > 1){
            this.fixedList = this._items[0].slice(0);
            this._items = this._items[1].slice(0);
        }else{
            this._items = this._items[0].slice(0);
        }

        let feedbackList = this._items.slice(0);
        this.feedbackList = this.sortByKey(feedbackList, "feedback");

        //
        let savedOrder = []
        if(this.isAnswered){
            //Get Saved Order
            for (let i = 0; i<this._items.length; i++){
                let val = this.calcService.getValueForYear('tlInput' + this.data['rangePrefix'] + (i + 1) + this.data['rangeSuffix'], this.yearRef);
                savedOrder.push(val);
            }

            //Update _items as per order
            for (let i = 0; i < this._items.length; i++) {
                let oldOrder = this._items[i].order;
                let newOrder = savedOrder.findIndex((value) => { return value == oldOrder });
                this._items[i].order = newOrder;
            }

            this.sortByKey(this._items, "order");
        }
     }

    updateValuesInModel() {
        for (let i = 0; i < this._items.length; i++) {
            this.calcService.setValueForYear('tlInput' + this.data['rangePrefix'] + (i + 1) + this.data['rangeSuffix'], this._items[i].order, this.yearRef);
        }
    }

    sortByKey(array, key) {
        return array.sort(function (a, b) {
            var x = a[key]; var y = b[key];
            return ((x < y) ? -1 : ((x > y) ? 1 : 0));
        });
    }

    onSubmit(){
        this.isAnswered = true;
        this.updateValuesInModel();
        this.calcService.setValue('tlInput' + this.id, 1);
        this.calcService.setValue('tlInput_READ_' + this.id, 1);
    }

    onContinue(alreadySubmitted: boolean) {
        this.continue.emit(alreadySubmitted)
    }

}


