import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subject } from 'rxjs/Rx';
import { TextHelperService } from '../../shared/text-helper.service';
import { TextService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from '../../shared/app-constants';
import * as _ from 'lodash';

@Component({
  selector: 'isma-quiz',
  templateUrl: './quiz.component.html',
  styleUrls: ['./quiz.component.styl']
})
export class QuizComponent implements OnInit {
    @Input() pinId: string = '';
    @Output() pinCompleted: EventEmitter<any> = new EventEmitter<any>();

    @Input() isReadOnly: boolean = false;

    private processedData: any;
    private quizScene: any;
    scenes: any = [];
    activeIndex = 0;

    constructor(private calcService: CalcService, 
        private textHelper: TextHelperService, 
        private textService: TextService,
        private communicator: CommunicatorService) { }

    ngOnInit() {
        // this.quizScene = this.textService.getScene(this.pinId);
        this.processedData = this.textHelper.getProcessedScenes();
        this.quizScene = _.filter(this.processedData, { ID: this.pinId })[0];

        let quizItems = this.quizScene['quizItems'] != undefined ? this.quizScene['quizItems'].split(',') : [];
        this.scenes = []
        for (let i = 0; i < quizItems.length; i++){
            let scene = _.filter(this.processedData, { ID: quizItems[i] })[0];
            if(scene){
                this.scenes.push(scene);
            }
        }
    }

    onContinue(alreadySubmitted: boolean){
        if(this.isReadOnly){
            return;
        }
        if(this.activeIndex == this.scenes.length - 1){
            if(!alreadySubmitted){
                this.pinCompleted.emit(this.pinId);
            }
            return;
        }

        this.activeIndex++;
        this.communicator.trigger(AppConstants.ANIMATE_MODAL, true)
    }

    onPrev(){
        if(this.activeIndex == 0){
            return;
        }

        this.activeIndex--;
    }

    onNext(){
        if(this.activeIndex == this.scenes.length - 1){
            return
        }

        this.activeIndex++;
    }

    getCurrentSceneType(){
        if(this.scenes[this.activeIndex]){
            const extraClass = (this.scenes[this.activeIndex].ExtraClass) ? this.scenes[this.activeIndex].ExtraClass : ''
            return this.scenes[this.activeIndex].DecisionType + ' ' + extraClass;
        }else{
            return null;
        }
    }

}



