import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subject } from 'rxjs/Rx';
import { TextHelperService } from '../../shared/text-helper.service';

@Component({
  selector: 'isma-multi-select',
  templateUrl: './multi-select.component.html',
  styleUrls: ['./multi-select.component.styl']
})
export class MultiSelectComponent implements OnInit {

  @Input() public data: any;
  @Input() yearRef: string = '';
  @Output() continue: EventEmitter<any> = new EventEmitter<any>();


  choices: any[] = [];
  answers = [];
  correctAnswers = [];
  id: string;
  scene: any;
  isAnswered: boolean = false;

  minChecks: number = 1;
  maxChecks: number;

  minSelected: boolean = false;
  maxSelected: boolean = false;

  public readyToSubmit = false;

  @Output() selectedAnswer: Subject<any> = new Subject<any>();

  constructor(private calcService: CalcService, private textHelper: TextHelperService) { }

    ngOnInit() {
        if (this.data) {
            this.id = this.data.ID;
            this.scene = this.data.scene;

            this.choices = this.textHelper.getMultiSelectChoices(this.id);

            let readValue = this.calcService.getValue('tlInput_READ_' + this.id);
            this.isAnswered = readValue == "0" ? false : true;

            for (let i = 0; i < this.choices.length; i++) {
                if (this.isAnswered){
                    let val = this.calcService.getValue(this.choices[i].range);
                    this.answers.push(val);
                }else{
                    this.answers.push('0');
                }

                this.correctAnswers.push(this.choices[i]['feedback']);
            }


            this.maxChecks = this.data["maxChecks"] ? +this.data["maxChecks"] : this.choices.length;
            this.minChecks = this.data["minChecks"] ? +this.data["minChecks"] : 0;

            let selected = this.answers.reduce((a, b) => { a + b });

            if (selected > this.maxChecks) {
                this.maxSelected = true;
            } else {
                this.maxSelected = false;
            }

            if (this.isAnswered) {
                this.onContinue(true);
            }
        }
    }

    onSelect(range, index) {
        this.answers[index] = this.answers[index] == "0" ? "1" : "0";

        let selected = this.answers.reduce((a, b) => { return parseInt(a) + parseInt(b) });

        this.maxSelected = selected >= this.maxChecks ? true : false;
        this.minSelected = selected < this.minChecks ? false : true;

        if(selected > this.maxChecks){
          this.answers[index] = "0";
          return;
        }

        // this.calcService.setValue(range, this.answers[index]);
        this.selectedAnswer.next(this.answers);
    }

    onSubmit() {
        this.isAnswered = true;
        //Set All Values to Model
        for(let i = 0; i < this.choices.length; i++){
          if(this.answers[i]){
            this.calcService.setValueForYear(this.choices[i].range, this.answers[i], this.yearRef);
          }
        }

        this.calcService.setValue('tlInput' + this.id, 1);
        this.calcService.setValue('tlInput_READ_' + this.id, 1);
    }

    onContinue(alreadySubmitted: boolean) {
        this.continue.emit(alreadySubmitted)
    }
}



