import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ScenarioComponent } from './scenario/scenario.component';
import { RoleComponent } from './role/role.component';
import { InstructionsComponent } from './instructions/instructions.component';
import { IntroComponent } from './intro/intro.component';
import { SharedModule } from '../shared/shared.module';
import { StartHoldComponent } from './start-hold/start-hold.component';
import { IntroStartedGuard } from './intro-started.guard';
import { IntroVisitedGuard } from './intro-visited.guard';

@NgModule({
  imports: [
    SharedModule
  ],
  declarations: [ScenarioComponent, RoleComponent, InstructionsComponent, IntroComponent, StartHoldComponent],
  exports: [ScenarioComponent, RoleComponent, InstructionsComponent, IntroComponent, StartHoldComponent],
  providers: [IntroStartedGuard, IntroVisitedGuard]
})
export class IntroModule { }



