import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { CalcService, CommunicatorService, Constants, NumberFormattingPipe, StorageService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'isma-text-metric',
  templateUrl: './text-metric.component.html',
  styleUrls: ['./text-metric.component.styl']
})
export class TextMetricComponent implements OnInit, OnDestroy {

  CURRENT_YEAR_REF = 'tlInputTeamYear';
  value: string;
  oldValue: string;
  lastYearValue: string;
  valueChanged = -1;
  subscription: Subscription;
  showLastYearRef = false;

  @Input() ref: string;
  @Input() currentYearRef: string;
  @Input() lastYearRef: string;
  @Input() format: string;
  @Input() scaler: number;
  @Input() metricPos: string;
  @Input() showBalloon = false;
  @Input() showMetricSeparator = false;
  @Input() balloonPlacement: string;

  constructor(private calcService: CalcService, private communicator: CommunicatorService,
    private numberFormatting: NumberFormattingPipe, private storageService: StorageService) { }

  ngOnInit() {
    if (this.currentYearRef !== void 0) {
      this.value = this.calcService.getValueForYear(this.ref, this.currentYearRef);
    } else {
      this.value = this.calcService.getValue(this.ref);

    }
    if (this.lastYearRef !== void 0) {
      this.lastYearValue = this.calcService.getValueForYear(this.ref, this.lastYearRef);
      this.showLastYearRef = true;
    } else {
      const curYear = this.calcService.getValue(this.CURRENT_YEAR_REF),
        ref = this.ref + '_year_' + curYear;
      this.storageService.getValue(ref)
        .then((value: string) => {
          if (value === null) {
            this.saveMetricOldValue(this.value);
          } else {
            this.lastYearValue = value;
            this.setValueChanged();
          }
        });
    }

    this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
      if (this.currentYearRef) {
        this.value = this.calcService.getValueForYear(this.ref, this.currentYearRef);
      } else {
        this.value = this.calcService.getValue(this.ref);
      }
      this.setValueChanged();
    });
    this.setValueChanged();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  private setValueChanged() {
    const curValue = this.numberFormatting.parse(this.value),
      curYear = this.calcService.getValue(this.CURRENT_YEAR_REF),
      lastYearValue = this.storageService.getValue(this.ref + '_year_' + curYear);
    let lastValue;
    if (this.lastYearRef) {
      lastValue = this.numberFormatting.parse(this.lastYearValue);
      this.updateValueChanged(lastValue, curValue);
    } else {
      lastYearValue.then((value: string) => {
        lastValue = this.numberFormatting.parse(value);
        this.updateValueChanged(lastValue, curValue);
      });
    }
  }

  updateValueChanged(lastValue, curValue) {
    if (curValue > lastValue) {
      this.valueChanged = 1;
    } else if (curValue < lastValue) {
      this.valueChanged = 2;
    } else if (curValue === lastValue) {
      this.valueChanged = 0;
    }
  }

  private saveMetricOldValue(value): Promise<any> {
    const curYear = this.calcService.getValue(this.CURRENT_YEAR_REF),
        ref = this.ref + '_year_' + curYear;
    return this.storageService.setValue(ref, value);
  }

}



