import { Component, Input, OnDestroy, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { CalcService, CommunicatorService, TextService, CalcStepperComponent as calcStpr, IsomerXapiService } from '@btsdigital/ngx-isomer-core';
import * as _ from 'lodash';
import { ModalDirective } from 'ngx-bootstrap';
import { Subscription } from 'rxjs/Rx';
import { AppConstants } from '../../shared/app-constants';
import { PageType } from '../../shared/page-type.enum';
import { TextHelperService } from '../../shared/text-helper.service';
import { QuizComponent } from '../../components/quiz/quiz.component';

@Component({
  selector: 'isma-pins-content',
  templateUrl: './pins-content.component.html',
  styleUrls: ['./pins-content.component.styl']
})
export class PinsContentComponent implements OnInit, OnDestroy {

  pinId: string;
  type: number;
  scene: any;
  clientId: string;
  pinImage: any;
  decisionType: string;
  currentPinType: string = '';

  @ViewChild('lgModal') childModal: ModalDirective;
  @ViewChild('quizInstance') quizInstanceRef: QuizComponent;

  isAnswered = false;
  animate = false;
  animateReverse = false;
  answer: any;
  isActive = false;
  actualAnswer;
  currentYear: string;
  private subscriptions: Subscription[] = [];
  openModal: boolean = false;
  animateOpen: boolean;
  showLoader: boolean = false;

  hasCaseStudy: boolean = false;
  caseStudyId = '';
  currentTab: number = -1;

  constructor(private calcService: CalcService, private cdr: ChangeDetectorRef,
    private communicator: CommunicatorService, private isomerXapiService: IsomerXapiService,
    private textHelper: TextHelperService) { }

  ngOnInit() {
    const openModalSubscribtion = this.communicator.getEmitter(AppConstants.OPEN_MODAL).subscribe((data: any) => {
      this.reset();
      this.pinId = data.pinId;
      this.type = data.type;
      this.scene = data.scene;
      this.clientId = data.clientId;
      this.pinImage = data.pinImage;
      this.decisionType = data.decisionType;
      this.caseStudyId = this.scene.CaseStudy;
      this.initialize();
      this.openModal = true;
      this.cdr.detectChanges();
      this.onSceneUpdate();
      this.showModal();
    });

    const timeUpSubscription = this.communicator.getEmitter(AppConstants.TIMER_UP_EVENT).subscribe(() => {
      if (this.isActive) {
        this.hideModal();
      }
    });

    const animateModalSubscription = this.communicator.getEmitter(AppConstants.ANIMATE_MODAL).subscribe((isReverse) => {
      this.onSceneUpdate();
      if (isReverse) {
        this.animateReverse = true;
      } else {
        this.animate = true;
      }
      setTimeout(() => {
        this.animateReverse = false;
        this.animate = false;
        this.cdr.markForCheck();
      }, 1000);
    });

    this.subscriptions = [openModalSubscribtion, timeUpSubscription, animateModalSubscription];
  }

  initialize() {
    this.currentYear = this.calcService.getValue('tlInputTeamYear');
    // this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.scene.pinTrigger), this.scene.pinTrigger, 1, this.currentYear, 'isma-pins-content');
    // this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.scene.pinDecisionVariable), this.scene.pinDecisionVariable, 0, this.currentYear, 'isma-pins-content', false);
    // this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.scene.pinViewedVariable), this.scene.pinViewedVariable, 0, this.currentYear, 'isma-pins-content', false);

    if(this.caseStudyId){
      this.hasCaseStudy = true;
      this.currentTab = 0;
    }
  }

  ngOnDestroy() {
    _.each(this.subscriptions, (n) => {
      if (n) {
        n.unsubscribe();
      }
    });
  }

  showModal() {
    this.animateOpen = true;
    this.childModal.show();
  }

  hideModal(setRead: boolean = false) {
    this.animateOpen = false;
    this.childModal.hide();
    this.animate = false;
    this.isActive = false;
    this.reset();
    this.communicator.trigger(AppConstants.CLOSE_MODAL);
  }

  onSubmit(answer: any) {
    this.isAnswered = true;

    // this.saveAnswer();
    this.showLoader = true;
    Promise.all([this.calcService.setValue(this.scene.pinDecisionVariable, 1, true),
      this.calcService.setValue(this.scene.pinViewedVariable, 1, true)]).then(()=>{
        this.showLoader = false;
        this.hideModal();
        this.animate = true;        
      })
  }

  saveAnswer() {
    this.calcService.setValue(this.scene.pinDecisionVariable, 1);
    this.calcService.setValue(this.scene.pinViewedVariable, 1);
  }

  onSceneUpdate(){
    this.currentPinType = this.quizInstanceRef ? this.quizInstanceRef.getCurrentSceneType(): '';
    this.cdr.markForCheck();
  }

  reset() {
    this.pinId = null;
    this.type = null;
    this.scene = null;
    this.clientId = null;
    this.pinImage = null;
    this.decisionType = null;
    this.hasCaseStudy = null;
    this.currentTab = null;

    this.showLoader = false;
    this.animate = false;
    this.animateReverse = false;
    this.openModal = false;
    this.isActive = false;
    this.isAnswered = false;

    this.answer = null;
    this.actualAnswer = null;
  }
}



