import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, ChangeDetectorRef, Output, EventEmitter, Input } from '@angular/core';
// import { Observable } from 'rxjs/Observable';
import { Observable, Subscription } from 'rxjs/Rx';
import * as moment from 'moment';
import 'moment-duration-format';
import { StorageService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from '../../shared/app-constants';

@Component({
  selector: 'isma-timer',
  templateUrl: './timer.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TimerComponent implements OnInit {

  @Input() startTime: number;
  // private subscription: Subscription;
  ticks: any = 0;
  timer: any = null;
  isInitialized = false;
  @Output() public onTimeUp: EventEmitter<any> = new EventEmitter<any>();

  constructor(private storageService: StorageService, private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
    this.startTimer();
  }

  startTimer(fallbackStartTime?: string) {
    this.storageService.getValue(AppConstants.ROUND_TIMER)
    .then(val => {
      if (val) {
        this.ticks = 0;
        this.startTime = Number(val);
        this.runTimer();
      } else if (fallbackStartTime) {
        this.ticks = 0;
        this.startTime = Number(fallbackStartTime);
        this.runTimer();
      }
    })
    .catch((err) => {
      if (fallbackStartTime) {
        this.ticks = 0;
        this.startTime = Number(fallbackStartTime);
        this.runTimer();
      } else {
        console.log('Could not start timer - as no fallbacktime was provided!');
      }
    });

    if (this.startTime) {
      this.runTimer();
    }
  }

  runTimer() {
    const self = this;
    let count = 0;

    if (self.startTime > 0) {
      this.isInitialized = true;

      if (this.timer) {
        clearInterval(this.timer);
      }

      this.timer = setInterval(function() {
        self.ticks = ((self.startTime) > 0) ? moment.duration(self.startTime, 's')
          .format('mm:ss', null, { 'trim': false }) : 'Time is up!';

        count++;
        self.cdRef.markForCheck();

        if (count > 5) {
          self.storageService.setValue(AppConstants.ROUND_TIMER, self.startTime + '');
          count = 0;
        }

        self.checkForTimeout();
        self.startTime--;
      }, 1000);
    }
  }

  stopTimer() {
    if (this.timer) {
      clearInterval(this.timer);
    }

    this.isInitialized = false;
    this.cdRef.markForCheck();
  }

  checkForTimeout() {
    if (this.isInitialized && this.ticks === 'Time is up!') {
      this.onTimeUp.emit(true);
      this.stopTimer();
      this.storageService.setValue(AppConstants.ROUND_TIMER, '0');
      this.cdRef.markForCheck();
    }
  }

}



