import {
  ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef,
  HostListener, OnDestroy, OnInit, ViewChild
} from '@angular/core';
import { CalcService, CommunicatorService, Constants, StorageService, TextService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap';
import { Subscription } from 'rxjs/Subscription';
import { environment } from '../../../environments/environment';
import { AppConstants } from '../../shared/app-constants';
import { TextHelperService } from '../../shared/text-helper.service';
declare var nw: any;

const KEY_NUM_1 = 49; // used to export csv data
const KEY_NUM_2 = 50; // used to bypass hold screen
const KEY_NUM_3 = 51; // used to bring up debugger modal
const KEY_NUM_4 = 52; // not in use
const PARTICIPANT_API_URL = '/Wizer/CloudFront/Participant';
const TIMER_START_VAL = {
  'ROUND_1': '2700', // 45 mins
  'ROUND_2': '2700', // 45 mins
  // 'ROUND_3': '1800' // 30 mins
};

@Component({
  selector: 'isma-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.styl'],
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class DashboardComponent implements OnInit, OnDestroy {
  clients: any = [];
  content: any[] = [];
  roundUpdated = false;
  REFS: any = {};
  feedbackDisabled = true;
  currentYear: string;
  passwordToChangeRound2: string;
  passwordToChangeRound3: string;
  changeRound = false;
  showRoundStartHold = false;
  showTimesUpHold = false;
  connectToPulse = environment.connectToPulse;
  private observer: Subscription;
  ismaclientData: any;
  headerData: any = {};
  el: HTMLElement;
  activeClients = [];


  @ViewChild('ismaheader') public ismaheader: any;

  constructor(private calcService: CalcService, private textService: TextService,
    private storageService: StorageService, private cdRef: ChangeDetectorRef,
    private communicator: CommunicatorService, private textHelper: TextHelperService, private elRef: ElementRef) { }

  ngOnInit() {
    this.el = this.elRef.nativeElement;

    this.currentYear = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
    const rawSceneData = this.textHelper.getRawScenes();
    this.content = rawSceneData.content;
    this.initClientsAndHeader();
    this.generateRefs();
    this.observer = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE)
      .subscribe(() => {
        this.calcCompleteSubscription();
      });
  }

  ngOnDestroy() {
    this.observer.unsubscribe();
  }

  // starts current round
  startRound() {
    this.calcService.setValue(AppConstants.REFS.START_SIM_FLAG, 1, false, this.currentYear, 'isma-dashboard').then(() => {
      this.cdRef.markForCheck();
    });
    // this.resetTimer();
  }

  initClientsAndHeader() {
    this.clients = [];
    this.activeClients = this.textHelper.getActiveClientsForRound(this.currentYear);

    const allClients = this.textHelper.getAllClients();
    this.clients = new Array(allClients.length).fill(1);

    this.headerData = {
    logo: 'assets/images/cisco-logo.png',
    title: 'Leader Day',
      simInfo: {
        showProgress: true,
        progressTitleKey: 'Progress',
        simInfoArray: [{
          key: 'Round',
          ref: 'tlInputTeamYear',
          class: 'round-info'
        }, {
          isTimer: true,
          key: 'Time',
          class: 'time-info'
        }]
      }
    };

    if(this.currentYear == '3'){
      this.headerData = {
      logo: 'assets/images/cisco-logo.png',
      title: 'Leader Day',
        simInfo: {
          showProgress: false,
          progressTitleKey: 'Progress',
          simInfoArray: [{
            key: 'Round',
            ref: 'tlInputTeamYear',
            class: 'round-info'
          }, {
            isTimer: true,
            key: 'Time',
            class: 'time-info'
          }]
        }
      };
    }
  }

  generateRefs() {
    this.REFS = [
      {
        numeralMetric: {
          rangeRef: AppConstants.PREFIX.CORPORATE_METRIC + '1' + AppConstants.SUFFIX.ROUND + this.currentYear,
          format: '0,0',
          titleRef: 'CorpMetric1',
          showBalloon: 'true'
        }
      }
    ];
  }

  calcCompleteSubscription() {
    this.roundUpdated = false;
    const year = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
    if (this.currentYear !== year) {
      this.roundUpdated = true;
      this.currentYear = year;
      this.showTimesUpHold = false;
      this.generateRefs();
      this.initClientsAndHeader();
      // this.resetTimer();
      this.communicator.trigger(AppConstants.CLOSE_EOR_FEEDBACK, true);
      // close end of round feedback / navigate to dashboard
      // this.router.navigateByUrl('/dashboard');
    }
  }
}



