import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subject } from 'rxjs/Rx';
import { TextHelperService } from '../../shared/text-helper.service';
import * as _ from 'lodash';
@Component({
  selector: 'isma-freetext-checkbox',
  templateUrl: './freetext-checkbox.component.html',
  styleUrls: ['./freetext-checkbox.component.styl']
})
export class FreeTextCheckboxComponent implements OnInit {
    @Input() public data: any;
    @Input() yearRef: string = '';
    @Output() continue: EventEmitter<any> = new EventEmitter<any>();

    choices: any[] = [];
    id: string;
    scene: any;
    answers = [];
    isAnswered: boolean = false;

    isSubmitEnabled = false;

    constructor(private calcService: CalcService, private textHelper: TextHelperService) { }

    ngOnInit() {
        if (this.data) {
            this.id = this.data.ID;
            this.scene = this.data.scene;

            const curVal = this.calcService.getValueForYear('tlInput' + this.id, this.yearRef);

            let readValue = this.calcService.getValueForYear('tlInput_READ_' + this.id, this.yearRef);
            this.isAnswered = readValue == "0" ? false : true;

            this.choices = this.textHelper.getMultiSelectChoices(this.id);

            this.answers = [];
            for (let i = 0; i < this.choices.length; i++) {
                if (this.isAnswered) {
                    let val = this.calcService.getValue(this.choices[i].range);
                    this.answers.push(val == '0' ? -1 : 1);
                } else {
                    this.answers.push(0);
                }
            }
            

            if (this.isAnswered) {
                this.onContinue(true);
            }
        }
    }

    onSelect($event) {
    }

    onSubmit() {
        this.isAnswered = true;
        //Set Checkbox Option Values
        for (let i = 0; i<this.answers.length; i++){
            let sliderValue = this.answers[i];
            let value;
            switch (sliderValue) {
                case -1:
                    value = "0";
                    break;
                case 1:
                    value = "1";
                    break;
            }
            this.calcService.setValueForYear(this.choices[i].range, value, this.yearRef);
        }

        //Set Question Values
        this.calcService.setValueForYear('tlInput' + this.id, 1, this.yearRef);
        this.calcService.setValueForYear('tlInput_READ_' + this.id, 1, this.yearRef);
    }

    onContinue(alreadySubmitted: boolean) {
        this.continue.emit(alreadySubmitted)
    }

    checkIfAbleToSubmit(val, index){
        this.answers[index] = val;

        const foundIndex = this.answers.findIndex((val) => { return val == 0 });
        this.isSubmitEnabled = foundIndex == -1 ? true : false;
    }

}


