import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { CalcService, CommunicatorService, Constants, TextService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs/Rx';
import { AppConstants } from '../../shared/app-constants';
import { PinsContentComponent } from '../pins-content/pins-content.component';

@Component({
  selector: 'isma-pins',
  templateUrl: './pins.component.html',
  styleUrls: ['./pins.component.styl']
})
export class PinsComponent implements OnInit, OnDestroy {

  @Input() pinId: string;
  @Input() type = 0;
  @Input() subType: string;
  @Input() viewed = false;
  @Input() trigger: string;
  @Input() visible = false;
  @Input() decisionType: string;
  @Input() scene: any;
  @Input() clientId: number = -1;
  @Input() pinImage: any;

  // @ViewChild(ContentHostDirective) contentHost: ContentHostDirective;
  @ViewChild(PinsContentComponent) pinsContent: PinsContentComponent;
  // @ViewChild('lgModal') childModal: ModalDirective;

  pinDecisionVariable: string;
  pinViewedVariable: string;
  sceneYear: number = -1;

  private subscription: Subscription;
  private communicatorSubscription: Subscription;
  private localPinTrigger: string;
  // private sliderMax: number = 0;
  // private ticks: any[] = [];
  // private tickLabels: string[] = [];
  private toggleStatus = false;

  constructor(private textService: TextService, private calcService: CalcService,
    private communicator: CommunicatorService, private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
    this.pinDecisionVariable = this.scene.pinDecisionVariable;
    this.pinViewedVariable = this.scene.pinViewedVariable;
    this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
      this.onModelChange();
    });
    this.communicatorSubscription = this.communicator.getEmitter(AppConstants.CLOSE_MODAL).subscribe(()=>{
      this.toggleStatus = false;
    });
    this.onModelChange();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
    if(this.communicatorSubscription){
      this.communicatorSubscription.unsubscribe();
    }
  }

  private onModelChange() {
    const currentYear = Number(this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND));
    this.sceneYear = -1;
    if (this.pinId && (this.pinId.length > 2)) {
      this.sceneYear = Number(this.pinId.slice(this.pinId.length - 1, this.pinId.length));
    }
    const activePin = (currentYear === this.sceneYear);
    // console.log ('onModelChanged: ' + this.pinId + ', ' + currentYear + ' == ' + sceneYear + ' = ' + (currentYear == sceneYear));
    // console.log (this.visible + ', ' + this.scene.pinTrigger + ' = ' + this.calcService.getValue(this.scene.pinTrigger));
    if (activePin && (this.visible === false) && (this.clientId === Number(this.scene.Client))) {
      if (this.scene !== null) {
        if ((this.scene.pinTrigger !== null)) {
          this.localPinTrigger = this.calcService.getValue(this.scene.pinTrigger);
          if (parseInt(this.localPinTrigger, 10) === 1) {
            this.visible = true;
            this.scene.visible = true;
          }
        }
      }
    }
    this.viewed = (Number(this.calcService.getValue(this.pinViewedVariable)) !== 0);
    // this.cdRef.markForCheck();
  }

  openContentModal() {
    const obj = {
      pinId: this.pinId,
      type: this.type,
      scene: this.scene,
      clientId: this.clientId,
      pinImage: this.pinImage,
      decisionType: this.decisionType
    };
    this.communicator.trigger(AppConstants.OPEN_MODAL, obj);
    // this.pinsContent.showModal();
  }

    toggleState(){
        this.toggleStatus = this.toggleStatus ? false : true;
    }
}



