import { Component, Input, OnInit, Output, EventEmitter, ViewChild } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subject } from 'rxjs/Rx';
import { TextHelperService } from '../../shared/text-helper.service';
import * as _ from 'lodash';
import { ToggleSliderComponent } from '../toggle-slider/toggle-slider.component';

@Component({
  selector: 'isma-truefalse-select',
  templateUrl: './truefalse-select.component.html',
  styleUrls: ['./truefalse-select.component.styl']
})
export class TrueFalseSelectComponent implements OnInit {

    @Input() public data: any;
    @Input() yearRef: string = '';
    @Output() continue: EventEmitter<any> = new EventEmitter<any>();

    @ViewChild('slider') sliderInstance: ToggleSliderComponent


    choices: any[] = [];
    answer = 0;
    correctAnswer:any;
    id: string;
    scene: any;
    isAnswered: boolean = false;
    isCorrect: boolean;

    constructor(private calcService: CalcService, private textHelper: TextHelperService) { }

    ngOnInit() {
        if (this.data) {
            this.id = this.data.ID;
            this.scene = this.data.scene;

            this.choices = this.textHelper.getSingleSelectChoices(this.id);

            let readValue = this.calcService.getValue('tlInput_READ_' + this.id);
            this.isAnswered = readValue == "0" ? false : true;

            let index = _.findIndex(this.choices, (obj) => { return obj['feedback'] == 'Correct' });
            this.correctAnswer = this.choices[index];

            const curVal = this.calcService.getValue('tlInput' + this.id);

            if (curVal && this.isAnswered) {
                switch (curVal) {
                    case 'F':
                        this.answer = -1;
                        break;
                    case 'T':
                        this.answer = 1;
                        break;
                    default:
                        this.answer = 0;
                        break;
                }

                this.isCorrect = this.correctAnswer.label == curVal ? true : false;
            }


            

            if(this.isAnswered){
                this.onContinue(true);
            }
        }
    }

    onSelect($event) {
    //
    }

    onSubmit(){
        this.isAnswered = true;

        let sliderValue = this.sliderInstance.getValue();
        let value;
        switch (sliderValue) {
            case -1:
                value = "F";
            break;
            case 1:
              value = "T";
            break;
        }

        this.isCorrect = this.correctAnswer.label == value ? true : false;

        this.calcService.setValueForYear('tlInput' + this.id, value, this.yearRef);
        this.calcService.setValueForYear('tlInput_READ_' + this.id, 1, this.yearRef);
    }

    onContinue(alreadySubmitted: boolean){
        this.continue.emit(alreadySubmitted)
    }
}



